  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
}

:root{
  --utama: #1A4D2E;
  --kedua: #4F6F52;
  --foot:  #1E1E1E;
  --hover: #FFC700;
  --tag:   #FFF455;
  
}

body{
  font-family: "Poppins", sans-serif;
  color: black;
}
/* skip link */
.link-fokus{
  position: absolute;
  top: -550px;
  left: 0;
  background-color: var(--hover);
  color: black;
  padding: 10px;
  z-index: 10000;
}

.link-fokus:focus{
  top: 0px;
}

/* navbar */
nav{
  font-weight: 200;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 50px;
  position: fixed;
  background-color: var(--utama);
  z-index: 999;
}

.hamburger{
  display: none;
}
.hamburger button{
  box-sizing: border-box;
  min-width: 44px;
  min-height: 44px;
  border: none;
  transition: 0.5s;
}
.hamburger button:hover{
  cursor: pointer;
  color: var(--hover);
}
.nav-logo{
min-width: 44px;
min-height: 44px;
font-size: 3em;
color: white;
}
nav ul li a{
  display: inline-block;
  min-height: 44px;
  padding: 1rem;
  min-width: 44px;
  color: white;
}
nav ul{
  display: flex;
  gap: 2rem;
  font-size: 24px;
  list-style: none;
  text-decoration: none;
}

nav ul li a:hover{
  background-color: var(--hover);
  border-radius: 8px;
  padding: 1rem;
  transition: ease-in-out .2s;
}

a:focus{
  outline: 3px solid black;
  padding: 2px;
  border-radius: 5px;
}
button:focus{
  outline: 3px solid black;
  padding: 2px;
  border-radius: 5px;
}
main{
  padding-top: 3rem;
}
/* jumbotron */
.Hero{
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: 41% 80%;
  filter: brightness();
}

.HeroIn{
  margin: 0 auto;
  max-width: 800px;
}

.judul-hero{
  color: white;
  font-weight: 500;
  font-size: 36px;
}

.tagline-hero{
  color: white;
  margin-top: 16px;
  font-size: 18px;
  font-weight: 300;
}

/* list resto */
.tempat-makan{
  color: var(--utama);
  padding: 4rem;
  text-align: center;
}
.list-tempat{
  display: grid;
  padding-top: 2rem;
  gap: 1rem;
  justify-content: center;
  grid-template-columns: repeat(auto-fit,minmax(300px,1fr));;
}

.judulResto:focus{
  outline: 4px solid black;
  padding: 2px;
  border-radius: 5px;
}

.list-tempat .judulResto{
  height: auto;
  width: auto;
  padding: 1rem;
  margin: 1rem;
  border-radius: 20px;
  color: white;
  background: rgb(26,77,46);
  background: linear-gradient(0deg, rgba(26,77,46,1) 0%, rgba(79,111,82,1) 31%, rgba(255,255,255,1) 82%);
  box-shadow: 0px 2px 5px black;
}

.list-tempat .judulResto .gambarResto img{
  min-height: 200px;
  width: 100%;
  border-radius: 10px;
}
.list-tempat .judulResto .gambarResto p{
  position: absolute;
  background-color: var(--utama);
  padding: 5px 10px;
  border-radius: 10px 0;
}
.list-tempat .judulResto .deskripsiResto p{
  text-align: left;
  padding-top: 0.2rem;
  font-weight: 200;
}
.list-tempat .judulResto .deskripsiResto a{
  padding: 1rem;
  min-height: 44px;
  min-width: 44px;
  font-size: 15px;
}

.list-tempat .judulResto .deskripsiResto .ratedesc{
  font-weight: 700;
  font-style: italic;
}

.list-tempat .judulResto .deskripsiResto .juduldesc{
  font-weight: 400;
}

footer{
  display: flex;
  font-weight: 200;
  padding: 0.5rem;
  font-size: 24px;
  color: white;
  background-color: var(--foot);
  text-align: center;
  align-items: center;
  justify-content: center;
}

/* tombol like */
.like {
  width: 55px;
  height: 55px;
  min-width: 44px;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* detail resto */

.detail-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.restoran-image {
  width: 100%;
  max-width: 800px;
  height: auto;
  min-height: 300px;
  margin-bottom: 1rem;
  border: 2px solid var(--hvr);
  border-radius: 50px;
}

.restoran-detail{
  display: grid;
}
.detail-description{
  background-color: rgb(171, 200, 183);;
  border-radius: 10px;
  padding: 1rem;
  margin: 2rem 0 2rem 0;
}

.judul-review{
  display: grid;
  padding-top: 2rem;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit,minmax(200px,1fr));;
}

.ulasan-item .review-item_bold{
  height: 200px;
  width: 200px;
  padding: 1rem;
  margin: 1rem;
  border-radius: 20px;
  color: black;
  background: rgb(26,77,46);
  background: linear-gradient(0deg, rgba(26,77,46,1) 0%, rgba(255,255,255,1) 31%, rgba(79,111,82,1) 82%);
  box-shadow: 0px 2px 5px black;
}

.menu{
  display: grid;
  justify-content: center;
  width: 100%;
  gap: 3rem;
  grid-template-columns: repeat(auto-fit,minmax(200px,1fr));;
}

.menu .makanan{
  text-align: center;
  height: 500px;
  width: 400px;
  padding: 1rem;
  margin: 1rem;
  border-radius: 20px;
  color: black;
  background: rgb(171, 200, 183);
  box-shadow: 0px 2px 5px black;
}

.menu .minuman{
  text-align: center;
  height: 500px;
  width: 400px;
  padding: 1rem;
  margin: 1rem;
  border-radius: 20px;
  color: black;
  background: rgb(171, 200, 183);
  box-shadow: 0px 2px 5px black;
}

.deskripsiResto a {
  color: white;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AACA;EACE,UAAU;EACV,SAAS;EACT,sBAAsB;EACtB,qBAAqB;EACrB,aAAa;AACf;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;EAChB,gBAAgB;EAChB,gBAAgB;EAChB,gBAAgB;;AAElB;;AAEA;EACE,kCAAkC;EAClC,YAAY;AACd;AACA,cAAc;AACd;EACE,kBAAkB;EAClB,WAAW;EACX,OAAO;EACP,8BAA8B;EAC9B,YAAY;EACZ,aAAa;EACb,cAAc;AAChB;;AAEA;EACE,QAAQ;AACV;;AAEA,WAAW;AACX;EACE,gBAAgB;EAChB,WAAW;EACX,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,kBAAkB;EAClB,eAAe;EACf,8BAA8B;EAC9B,YAAY;AACd;;AAEA;EACE,aAAa;AACf;AACA;EACE,sBAAsB;EACtB,eAAe;EACf,gBAAgB;EAChB,YAAY;EACZ,gBAAgB;AAClB;AACA;EACE,eAAe;EACf,mBAAmB;AACrB;AACA;AACA,eAAe;AACf,gBAAgB;AAChB,cAAc;AACd,YAAY;AACZ;AACA;EACE,qBAAqB;EACrB,gBAAgB;EAChB,aAAa;EACb,eAAe;EACf,YAAY;AACd;AACA;EACE,aAAa;EACb,SAAS;EACT,eAAe;EACf,gBAAgB;EAChB,qBAAqB;AACvB;;AAEA;EACE,8BAA8B;EAC9B,kBAAkB;EAClB,aAAa;EACb,2BAA2B;AAC7B;;AAEA;EACE,wBAAwB;EACxB,YAAY;EACZ,kBAAkB;AACpB;AACA;EACE,wBAAwB;EACxB,YAAY;EACZ,kBAAkB;AACpB;AACA;EACE,iBAAiB;AACnB;AACA,cAAc;AACd;EACE,aAAa;EACb,mBAAmB;EACnB,iBAAiB;EACjB,WAAW;EACX,kBAAkB;EAClB,yDAAgE;EAChE,4BAA4B;EAC5B,oBAAoB;AACtB;;AAEA;EACE,cAAc;EACd,gBAAgB;AAClB;;AAEA;EACE,YAAY;EACZ,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,YAAY;EACZ,gBAAgB;EAChB,eAAe;EACf,gBAAgB;AAClB;;AAEA,eAAe;AACf;EACE,mBAAmB;EACnB,aAAa;EACb,kBAAkB;AACpB;AACA;EACE,aAAa;EACb,iBAAiB;EACjB,SAAS;EACT,uBAAuB;EACvB,yDAAyD;AAC3D;;AAEA;EACE,wBAAwB;EACxB,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,WAAW;EACX,aAAa;EACb,YAAY;EACZ,mBAAmB;EACnB,YAAY;EACZ,yBAAyB;EACzB,sGAAsG;EACtG,6BAA6B;AAC/B;;AAEA;EACE,iBAAiB;EACjB,WAAW;EACX,mBAAmB;AACrB;AACA;EACE,kBAAkB;EAClB,8BAA8B;EAC9B,iBAAiB;EACjB,qBAAqB;AACvB;AACA;EACE,gBAAgB;EAChB,mBAAmB;EACnB,gBAAgB;AAClB;AACA;EACE,aAAa;EACb,gBAAgB;EAChB,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,gBAAgB;EAChB,eAAe;EACf,eAAe;EACf,YAAY;EACZ,6BAA6B;EAC7B,kBAAkB;EAClB,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA,gBAAgB;AAChB;EACE,WAAW;EACX,YAAY;EACZ,eAAe;EACf,gBAAgB;EAChB,aAAa;EACb,